<template>
  <div class="bag-details">
    <div class="heard">

      <div class="left-heard">
        <div>
          <img :src="nftDetailInfo.resUrl" alt="" v-if="status"/>
          <img src="../../images/box.jpg" alt="" v-else>
        </div>
      </div>

      <div class="right-heard">
        <div class="title">NFT#{{ nftDetailInfo.id }}</div>
        <div class="title-list" v-if="status">
          <div class="head" >
            <div >{{language.label_lv}}</div>
          </div>

          <div class="head-value" >
            <div>Lv.{{ nftDetailInfo.grade }}</div>
          </div>
        </div>

        <div class="title-list" v-if="!status">
          <div class="head" >
            <div>名字</div>
          </div>
          <div class="head-value" >
            <div>{{name}}</div>
          </div>
        </div>


        <div class="attribute">
          <div class="attribute-title">{{language.label_attr}}</div>
          <div class="box box-txt">
            <div class="">{{language.label_2_1}}</div>
            <div class="" v-if="status">{{language.label_2_2}}</div>
              <div class="" v-if="!status">价格</div>
          </div>
          <div class="box box-color">
            <div class="" v-if="status">{{  dateFormatter(nftDetailInfo.createdTime).format('yyyy-MM-dd')   }}</div>
            <div class="" v-else>{{  dateFormatter(nftDetailInfo.startAt).format('yyyy-MM-dd')   }}</div>
            <div class="" v-if="status">{{  blanceFn(nftDetailInfo.amount)  }}</div>
             <div class="" v-if="!status" >{{parseInt(price).toFixed(4)}}</div>
          </div>
          <div class="box box-txt" v-if="status">
            <div class="">{{language.label_2_3}}</div>
            <div class="">{{language.label_2_4}}</div>
          </div>
          <div class="box box-color" v-if="status">
            <div class="">{{ nftDetailInfo.blockNum }}</div>
            <div class="addre">{{ nftDetailInfo.author }}</div>
          </div>
        </div>

        <div class="cards">
          <div>
            <m-button
              style="width:100px"
              class="btn hdr-el-blue"
              :loading="giveNftLoading"
              @click="complimentaryCards"
            >
              <span>{{status?language.btn_give_card:language.complimentary_blind_box }}</span>
            </m-button>
          </div>

          <div v-if="!status">
            <m-button
              style="width:100px"
              class="btn hdr-el-blue"
              :loading="openLoading"
              @click="openBlindBoxFn"
            >
              <span>{{language.open_stery_box}}</span>
            </m-button>
          </div>


          <div>
            <m-button
              style="width:100px"
              class="btn hdr-el-blue"
              :loading="giveNftLoading"
              @click="sellingCards"
            >
              <span>{{status?language.sell_cards:language.sell_blind_box}}</span>
            </m-button>
          </div>
        </div>
      </div>
    </div>

    <div class="upgradeCards" v-if="status">
      <div class="cards-title">{{language.upgrade_card}}</div>
      <div class="grade">
        <div>{{language.current_love}} {{nftDetailInfo.grade}} -> {{language.expected_upgrade}}{{ parseInt(nftDetailInfo.grade) +1}}</div>
        <div class="upgrade-box">

          <m-button
           style="width:100px;margin-left:10px"
            v-if="!isWarrant"
            class="btn hdr-el-blue"
            :loading="giveNftLoading"
            @click="warrantFn"
          >
            {{language.btn_authorize}}
          </m-button>

          <m-button
            style="width:100px;margin-left:10px"
            v-else
            class="btn hdr-el-blue"
            :loading="giveNftLoading"
            @click="upgradeFn"
          >
            {{language.upgrade_c}}
          </m-button>

           

        </div>
      </div>
      <div class="tips">{{language.level_to_upgrade}}</div>

      <div class="upgrade-img-list">

        <div class="upgrade-img" v-for="(item, index) in arrid" :key="index">
          <div class="lev">Level {{ arrgarid[index] }}</div>
          <img :src="url[index]" alt="" />
        </div>

        <div
          class="upgrade-img"
          v-for="(item, index) in 5-arrid.length"
          :key="index"
          @click="choiceFn"
        >
         <div class="LevelBox">
            <div class="lev">Level</div>
            <div class="onNft">NFT</div>
         </div>
        </div>

      </div>

    </div>
    <complimentaryCards
       :status="status"
      :cardsStatus="cardsStatus"
      @givAddressFn="givAddressFn"
      :giveAdd="giveAdd"
    >
    </complimentaryCards>

    <sellingCards
      :status="status"
      :sellingStatus="sellingStatus"
      :sellingPopup="sellingPopup"
      @sellingFn="sellingFn"
    >
    </sellingCards>

    <!-- 卡牌列表 -->
    <cardsList
      :cardsListStatus="cardsListStatus"
      @onCardsFn="onCardsFn"
    ></cardsList>

    <!-- 打开盲盒 -->
   <openBildBox 
     :quality="quality"
     :openBildBoxStatus="openBildBoxStatus" 
     @closeMyFnSterBox="closeMyFnSterBox"
   ></openBildBox>
        
    

  </div>
</template>

<script>

 
 

import { mapActions, mapGetters, mapState } from "vuex";
// import MPopper from "@/components/mPopper";
import MButton from "@/components/mButton";
import {BN} from "@/common/util";
import complimentaryCards from "@/views/nft/comps/complimentaryCards";
import sellingCards from "@/views/nft/comps/sellingCards";
import cardsList from "@/views/nft/comps/cardsList";
import {dateFormatter} from "@/common/dataConvertor";
import openBildBox from "./comps/openBildBox";
export default {
  name: "nftBag",
  components: { MButton, complimentaryCards,openBildBox ,sellingCards, cardsList,},
  computed: {
    ...mapState("app", ["address"]),
    ...mapState("nft", ["myCards"]),
    ...mapGetters(["language"]),
  },
  watch: {},
  data() {
    return {
      status:false,
      nftDetailInfo: {},
      cardsStatus: false,
      giveNftLoading: false,
      giveAdd: false,
      sellingStatus: false,
      sellingPopup: false,
      cardsListStatus: false,
      arrid:[],
      arrgarid:[],
      url:[],
      isWarrant:false,
      name:"",
      price:"",
      openLoading:false,
      openBildBoxStatus:false,
      num:"",
      quality:"",
    };
  },
  mounted() {
     this.$nextTick(()=>{
       this.initBalance();
       if( this.$route && this.$route.query && this.$route.query.user){
            this.status = false
            this.nftDetailInfo = JSON.parse( this.$route.query.user)
            let str = this.nftDetailInfo.boxFactory
            this.name = str.name
            this.price = str.price
       }else{
             this.nftDetailInfo = this.$route.query
              this.status = true
       }
      // let aa  = JSON.parse(str)
      // console.log(aa)
    
       this.IsApprovedFn();
    })
  },
  methods: {
    dateFormatter,
    ...mapActions("nft", ["setMyCards", "deleteCard"]),

    blanceFn(data){
        let blan =   new BN(data).toDisplay("balance").toString(10)
        let price =  Number(blan).toFixed(4)
        return price
    },
    // 打开盲盒
    openBlindBoxFn(){
          this.buttonToFn();
    },
     initBalance(){
       this.$api.balanceOfFn().then((res)=>{
          this.num = res
       });
    },
     //开盲盒
    buttonToFn(){
      if( this.num <=0){
         return this.$toast.error("请购买盲盒");
      }
      this.openBildBoxStatus = true;
      this.openLoading = true;
       this.$api.queryBoxFn().then(( {mysteryBoxes})=>{
            this.startFn(mysteryBoxes[0].tokenId)
       }).catch(()=>{
           this.openLoading = false;
           this.openBildBoxStatus = false;
       });
    },
     startFn(id){
        this.$api.openStartBoxFn(id).then(()=>{
             this.finlaResult(id)
             this.initBalance()
        }).catch(()=>{
           this.openLoading = false;
            this.openBildBoxStatus = false;
       });
     },
    finlaResult(id){
          this.$api.getOpenBoxFn(id).then((res)=>{
          this.quality  = res.quality;
          this.openLoading = false;
        }).catch(()=>{
           this.openLoading = false;
            this.openBildBoxStatus = false;
       });
    },

    // 子组件 方法  赠送卡牌
    givAddressFn(addressData) {
      if (addressData) {
        this.giveAdd = true;
         if(this.status){
           this.giveNftAddressFn(addressData);
         }else{
           this.giveNftBlindBoxFns(addressData)
         }
       
      } else {
        this.giveAdd = false;
        this.cardsStatus = false;
      }
    },

  //  赠送的方法核心nft
    giveNftAddressFn(addressData){
      this.$api.giveNft( this.address,addressData.toLowerCase(),this.nftDetailInfo.id)
        .then(() => {
          this.giveAdd = false;
          this.cardsStatus = false;
        })
        .catch(() => {
          this.giveAdd = false;
          this.cardsStatus = false;
        });
    },
    // 盲盒赠送的方法核心nft
    giveNftBlindBoxFns (addressData){
      this.$api.giveBlindBoxNft( this.address,addressData.toLowerCase(),this.nftDetailInfo.id )
        .then(() => {
          this.giveAdd = false;
          this.cardsStatus = false;
        })
        .catch(() => {
          this.giveAdd = false;
          this.cardsStatus = false;
        });
    },
      

    // 子组件 出售卡牌的方法
    sellingFn(data) {
      if (data) {
          this.sellingPopup = true;
          this.sellCarFn(data);
      } else {
         this.sellingStatus = false;
         this.sellingPopup = false;
      }
    },
    //  出售卡牌
    sellCarFn(data) {
      let name = ""
      if(this.status){
          name = "NFTToken"
      }else{
          name = "mysterybox"
      }
      let  sellingPriceMax = new BN(data.sellingPriceMax).toWei("balance").toString(10)
      let sellingPriceMin = new BN(data.sellingPriceMin).toWei("balance").toString(10)
      this.$api.sellCardsFn(this.nftDetailInfo.id,sellingPriceMax,sellingPriceMin,data.timeSeconds,data.time,name )
        .then(() => {
          this.sellingPopup = false;
          this.sellingStatus = false;
          this.$router.push({
            path:"/nft/bag"
          })
        }).catch(()=>{
              this.sellingPopup = false;
        });
    },

    // 赠送卡牌
    complimentaryCards() {
      this.cardsStatus = true;
      this.giveAdd = false;
    },
    // 出售卡牌
    sellingCards() {
      this.sellingStatus = true;
    }, // 子组件 卡牌列表
    onCardsFn(arrid,arrgarid,url) {
      if(arrid && arrgarid && url){
          this.arrid = arrid;
          this.arrgarid = arrgarid;
          this.url = url;
      }
      this.cardsListStatus = false;
    },
    // 卡牌列表
    choiceFn() {
      this.cardsListStatus = true;
    },
    closeMyFnSterBox(){
        this.openLoading = false;
        this.openBildBoxStatus = false;
    },
    // 升级
    upgradeFn() {
       let id =  this.nftDetailInfo.id ;
        this.$api.upgradeFn(id, this.arrid).then((res)=>{
              console.log(res)
              this.arrid = [];
              this.arrgarid =[];
              this.url = [];
              this.upgradeResults();
        }).catch(()=>{
            this.$toast.error(this.language.network_anomaly);
        })
    },
     upgradeResults(){
          this.$api.upgradedLastNftFn().then((res)=>{
            if(res !=0){
                this.$toast.success(this.language.update_successed)
            }else{
                this.$toast.error(this.language.upgrade_failed)
            }
             this.$router.push('/nft/bag')
          }).catch(()=>{
            this.arrid = [];
            this.arrgarid =[];
            this.url = [];
            this.$toast.error(this.language.network_anomaly);
            this.$router.push('/nft/bag')
        })
           
     },
    // 

     // checkNFTIsApproved
    // 判断是否授权
    IsApprovedFn() {
      this.$api.checkNFTIsApproved("NFTTokenFactory","NFTToken").then((res) => {
        this.isWarrant = res;
      });
    },
    // 授权
    warrantFn() {
      this.$api.setNFTApprovalForAll("NFTToken","NFTTokenFactory").then(() => {
        this.isWarrant = true;
      });
    },
  },
   beforeUnmount(){
      clearInterval(this.timer);// 清除定时器
      this.timer = null;
   },
};
</script>

