<template>
  <div class="dialog" v-show="sellingStatus">
    <div class="dialog-container">
      <div class="dialog-title"> {{ status?language.sell_cards:language.sell_blind_box}}</div>
      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="goBack()" />
      </div>
      <div class="lowest">
        <input type="text" :placeholder="language.sale_lowest_price" v-model="sellingPriceMin" />
        <div class="">DFT</div>
      </div>
      <div class="lowest">
        <input type="text" :placeholder="language.sell_highest_price" v-model="sellingPriceMax" />
        <div class="">DFT</div>
      </div>
      <div class="lowest">
        <div class="txt-title"> {{language.intervals}}</div>
        <div class="time">
          <select v-model="time">
            <option class="myoption" value="300">{{language.resolution_5}}</option>
            <option class="myoption" value="600">{{language.resolution_15}}</option>
            <option class="myoption" value="900">{{language.resolution_15}}</option>
          </select>
        </div>
      </div>

      <div class="box-text">
        <div class="text">
          {{ status ?language.view_for_sale:language.sell_blid}}
        </div>
        <div class="text">
          {{status ? language.after_success:language.not_be_retrieved}}
        </div>
      </div>

      <div class="btn">
        <m-button
          v-if="!isWarrant"
          :loading="sellingPopup"
          @click="warrantFn"
          class="hdr-el-blue"
          :height="36"
          style="width: 148px"
        >
           {{language.btn_authorize}}
        </m-button>

        <m-button
          v-else
          :loading="sellingPopup"
          @click="giveFn"
          class="hdr-el-blue"
          :height="36"
          style="width: 148px"
        >
          {{language.confirm_sale}}
        </m-button>
      </div>
    </div>
  </div>
</template>

<script>
import MButton from "@/components/mButton";
import { mapState, mapGetters } from "vuex";
export default {
  components: { MButton },
  props: {
    sellingStatus: {
      type: Boolean,
      default: false,
    },
    sellingPopup: {
      type: Boolean,
      default: false,
    },
    status:{
       type: Boolean,
       default: false,
    }
  },
  computed: {
    ...mapState("app", ["address"]),
    ...mapState("nft", ["myCards"]),
    ...mapGetters(["language"]),
  },
  data() {
    return {
      sellingPriceMin: "",
      sellingPriceMax: "",
      time: "5",
      isWarrant: false,
      isShow:false,
    };
  },
  mounted() {
    this.$nextTick(()=>{
       if(this.$route && this.$route.query && this.$route.query.user){
           this.isShow =  true;
       }else{
           this.isShow = false;
       }
      if(this.isShow){
          this.IsApprovedFn("nftmarket","mysterybox");
      }else{
          this.IsApprovedFn("nftmarket","NFTToken");
      }
      
        
    })
  },
  methods: {
    // checkNFTIsApproved
    // 判断是否授权
    IsApprovedFn(data,name) {
      this.$api.checkNFTIsApproved(data,name).then((res) => {
        this.isWarrant = Number(res) ;
      });
    },
    // 授权
    warrantFn() {
      if(this.isShow){
        this.$api.setNFTApprovalForAll("mysterybox","nftmarket").then(() => {
           this.isWarrant = true;
         });
      }else{
       this.$api.setNFTApprovalForAll("NFTToken","nftmarket").then(() => {
        this.isWarrant = true;
      });
      }
      
    },
    giveFn() {
      let { sellingPriceMin, sellingPriceMax, time } = this;
      if (!sellingPriceMin) {
        return this.$toast.error(this.language.cannot_be_empty);
      }
      if (!sellingPriceMax) {
        return this.$toast.error(this.language.maximum_price);
      }
     
      if (Number(sellingPriceMax)  <= Number (sellingPriceMin)) {
        return this.$toast.error(this.language.minimum_sale_price);
      }
      let timeSeconds = Date.parse(new Date()) / 1000;
      let datas = {
        sellingPriceMin: sellingPriceMin,
        sellingPriceMax: sellingPriceMax,
        timeSeconds: timeSeconds,
        time: time,
      };
      this.$emit("sellingFn", datas);
    },
    goBack() {
      this.$emit("sellingFn");
    },
  },
};
</script>
