  props: {
    openBildBoxStatus: {
      type: Boolean,
      default: false,
    },
    quality:{
      type: Number,
      default: 0,
    }
  },
  computed: {
    ...mapState('app',['address']),
    ...mapGetters(['language']),
  },
   watch:{
    quality(){
      this.status = true,
      setTimeout(()=>{
           this.status = false;
      },0)
    }
  },
  data() {
    return {
        status:true,
    };
  },
  mounted() {
    this.$nextTick(()=>{
       
    })
  },
  methods: {
    // 返回
    goBack(){
      if(!this.status ){
          this.$router.push({path:"/nft/bag"})  
      }
      this.$emit("closeMyFnSterBox");
    },
  },
};
</script>
