<template>
  <div class="dialog" v-show="cardsStatus">
    <div class="dialog-container">
      <div class="dialog-title"> {{ status?language.btn_give_card:language.complimentary_blind_box }}</div>
      <div class="close">
        <img src="../../../images/close@2x.png" alt="" @click="goBack()" />
      </div>
      <div class="giveNftInput">
        <input
          type="text"
          :placeholder="language.wallet_address"
          v-model="giveAddress"
        />
      </div>
      <div class="btn">
          <m-button :loading="giveAdd" @click="giveFn" class="hdr-el-blue" :height="36"
            style="width: 148px"
          >
            {{language.make_sure_give}}
      </m-button>
      </div>
     
    </div>
  </div>
</template>

<script>
import MButton from "@/components/mButton";
import { mapState, mapGetters } from "vuex";
export default {
  components: { MButton },
  props: {
    cardsStatus: {
      type: Boolean,
      default: false,
    },
     giveAdd:{
        type: Boolean,
        default: false,
    },
    status:{
        type: Boolean,
        default: false,
    }
   
   
  },
  computed: {
    ...mapState("app", ["address"]),
    ...mapState("nft", ["myCards"]),
    ...mapGetters(["language"]),
  },
  data() {
    return {
      giveAddress: "",
    };
  },
  mounted() {},
  methods: {
    giveFn() {
       this.$emit("givAddressFn",this.giveAddress)
    },
    goBack(){
         this.$emit("givAddressFn");
    },
  },
};
</script>
