<template>
  <div class="dialog" v-show="cardsListStatus">
    <div class="dialog-container">
      <div class="dialog-title" @click="goBack"> 
            <img src="../../../images/luck/jian.png" alt="">
               {{language.nftBackpack}}
        </div>

      <div class="knapsack">
       
        <div v-for="(item, index) in myCardsFiltered" :key="index" @click="getDefineFn(index,item.id,item)">
          <img :src="item.resUrl" alt=""  class="img1"/>
          <img src="../../../images/confirm.png" alt=""  class="img2" v-if="groupIds.includes(item.id)"/> 
          <p>#{{ item.id }}</p> 
        </div>
      </div>



      <div  class="chooseGuard">* {{language.guardian}}</div>
      <div  class="everyday" >* {{language.afterAdding}}</div>
    

      <div class="preservation" @click="closeFn">
        <m-button class="hdr-el-blue" :height="36" style="width: 148px">
           {{language.sure}}
        </m-button>
      </div>
    </div>

    <ul>
      <li v-if="myCardsFiltered.length===0&&!bagLoading" class="no-data flex jc-center ai-center">
          <p class="text fs-16 ta-center">
            <img src="../../../images/no-data.png" alt="nodata"><br>
            {{language.nodata}} NFT
          </p>
        </li>
        <li v-if="bagLoading" class="mask flex jc-center ai-center">
          <i class="icon-loading"></i>
      </li>
    </ul>
    


  </div>
</template>

<script>
import MButton from "@/components/mButton";
import {mapState,mapGetters} from "vuex";
export default {
  components: { MButton },
  props: {
    cardsListStatus: {
      type: Boolean,
      default: false,
    },
    typeStatus:{
       type:String,
       default: "",
    }
  },
  computed: {
    ...mapState('app',['address']),
    ...mapGetters(['language']),
  },
   watch:{
    address(){
      this.getCardList();
    }
  },
  data() {
    return {
       numText:'',
      list: 31,
      signStatus: [8, 2, 20, 15, 4],
      statusIndex:"",
      id:"",
      url:"",
      groupIds:[],
      gardIndex:[],
      resUrlArr:[],
      myCardsFiltered:[],
    };
  },
  mounted() {
    this.$nextTick(()=>{
        this.getCardList();
    })
  
  },
  methods: {
      getCardList() {
      this.$api.gqlBagNftListFieldPack(this.address,this.$route.query.quality).then((res)=>{
          for (let i = 0; i < res.defxTokens.length; i++) {
            if(res.defxTokens[i].id != this.$route.query.id ){
              this.myCardsFiltered.push(res.defxTokens[i]) 
            }
          }
       
      }).catch(()=>{
      });
    },
    // 确定
    closeFn() {
      if(this.groupIds.length == 0){
        return this.$toast.error(this.language.card_to_upgrade);
      }
      if(this.groupIds.length < 5 ){
        return this.$toast.error(this.language.please_5_cards);
      }
      this.$emit("onCardsFn",this.groupIds,this.gardIndex,this.resUrlArr );
        
    },
    // 返回
    goBack(){
         this.$emit("onCardsFn");
    },
    // 选择头像
    getDefineFn(index,id,item){
        let _this = this;
        let arr = _this.groupIds;
        if(arr.includes(id)){
          //includes()方法判断是否包含某一元素,返回true或false表示是否包含元素，对NaN一样有效
            let index = arr.indexOf(id); // 判断数组中是否有选中的id,如果有则去掉
            if(index > -1){
             arr.splice(index,1)
             _this.gardIndex.splice(index,1)
             _this.resUrlArr.splice(index,1)
            }
        }else{
            if(_this.groupIds.length >4){
                return  this.$toast.error(this.language.already_exceeded);
            }
            _this.resUrlArr.push(item.resUrl)
            _this.gardIndex.push(item.grade)
            _this.groupIds.push(id)
        }
        console.log(_this.gardIndex);
        console.log(_this.groupIds);

          // this.statusIndex = index;
          // this.id = id;
          // this.url = url;
    },
  },
};
</script>
